import {connect} from "react-redux";
import store from "../../store";
import EditTargetRow from "./EditTargetRow";

const Targets = () => {
    let targets = store.getState().siteModelChecklist?.currentChecklistTargets ? store.getState().siteModelChecklist?.currentChecklistTargets : [];
    let currentChecklist = store.getState().siteModelChecklist?.currentChecklist;
    targets.push({checklistuuid:currentChecklist?.id,parentid:0,active:1});
    return currentChecklist?.id ? <div>
        <table className={"table table-bordered"}>
            <thead>
                <th></th>
                <th>Name</th>
                <th></th>
            </thead>
            <tbody>
            {targets.filter((item)=>{
               return item.parentid===0
            }).map((item,idx)=>{
                return <EditTargetRow item={item} idx={idx} key={item.checklistuuid + item.id} />
            }) }

            </tbody>
        </table>
    </div> : <div> Please Choose a checklist to view.</div>
}

const mapStateToProps = (state) =>{
    return {
        siteModelChecklist:state.siteModelChecklist,
        dataStore:state.dataStore,
        tabStore:state.tabStore

    };
};

export default connect(mapStateToProps,{})(Targets);