import store from "../../store";
import {updateSiteModelChecklist} from "../../actions/cbChecklist_Actions";

const addChild = (checklistuuid,parentid,displayOrder) => {
    let allTargets = store.getState().siteModelChecklist?.currentChecklistTargets ? store.getState().siteModelChecklist?.currentChecklistTargets : [];
    allTargets.push({checklistuuid:checklistuuid,displayOrder:displayOrder,parentid:parentid,isDirty:true});
    store.dispatch(updateSiteModelChecklist({currentChecklistTargets:allTargets}));
}

const CreateEditChildButton = ({checklistuuid,parentid,displayOrder}) => {
    return <button onClick={()=>addChild(checklistuuid,parentid,displayOrder)}> Add Child </button>
}

export default CreateEditChildButton;