import {useState} from "react";
import { saveChecklistTarget} from "../../containers/cbChecklist/checkListMethods";
import CreateEditChildButton from "./CreateEditChildButton";
import store from "../../store";



const EditTargetRow = ({item,idx,indent=0}) => {
    item.displayorder = item.hasOwnProperty("displayorder") && isNaN(item.displayorder) ? item.displayorder : idx+1;
    let [checkItem,setCheckItem] = useState(item);
    let bgcolor = checkItem.hasOwnProperty("isDirty") && checkItem.isDirty ? "yellow" : "none";
    let saveButton = checkItem?.isDirty ? <button className={"btn btn-sm btn-secondary"} onClick={()=>{setCheckItem(Object.assign({},checkItem,{"isDirty":false})); saveChecklistTarget(checkItem)}}>Save</button> : null;
    let allTargets = store.getState().siteModelChecklist?.currentChecklistTargets ? store.getState().siteModelChecklist?.currentChecklistTargets : [];
    let childs = allTargets.filter((target)=>{
        return target.parentid===item.id;
    });
    let indentTypes = [
      "left",
      "center",
      "right"
    ];
    return (
        <>
        <tr style={{backgroundColor:bgcolor}}>
            <td style={{textAlign:indentTypes[indent]}}>{idx+1}</td>
            <td>
                <input type={"text"} value={checkItem.testpackage}
                       onChange={(evt)=>setCheckItem(Object.assign({},checkItem,{"testpackage":evt.currentTarget.value,isDirty:true}))}
                       style={{paddingLeft:(indent*10) + "%", width:"100%"}} />
            </td>
            <td>{saveButton}</td>
            <td><CreateEditChildButton checklistuuid={item.checklistuuid} parentid={item.id} displayOrder={childs.length+1}  /></td>
        </tr>
            {childs.map((child,idx)=>{
                return <EditTargetRow item={child} idx={idx} key={child.parentid + child.diplayOrder} indent={indent+1} />
            })}

        </>
    )
}

export default EditTargetRow;